import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

function useDialogs() {
    const conf = {
        title: "",
        context: "",
        confirm: {
          handle: () => false,
          msg: ""
        }
    }
  const [open, setOpen] = React.useState(false);
  const [option, setOption] = React.useState(conf);

  const handleClickOpen = (conf) => {
    setOption({...option, ...conf})
    setOpen(true);
  };

  const handleClose = () => {
    setOption(conf)  
    setOpen(false);
  };

  const Componet = () => (
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-labelledby="alert-dialog-slide-title"
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle id="alert-dialog-slide-title">{option.title}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-slide-description">
                {option.context}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          {/* <Button onClick={handleClose} color="primary">
                {option.cancel}
          </Button> */}
          <Button onClick={option.confirm?.handle ? option.confirm?.handle : () => false } color="primary">
                {option.confirm?.msg}
          </Button>
        </DialogActions>
      </Dialog>
  );

    return [handleClickOpen, handleClose, Componet]
    
}

export { useDialogs }