import React from 'react';

import { 
    Container, 
    Main,
} from '../../styles/Layout';

import {
    CardServico, 
    useStyles,
    WrapperCardMedia,
    WrapperCalendario,
    WrapperListHorario,
    WrapperInfosAgenda,
    InfosAgenda,
    WrapperButton,
    WrapperProfileClient,
    WrapperProfileinfo,
    WrapperProfileinfoList
} from './styles';

import Avatar from '@material-ui/core/Avatar';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import CorteCAbeloM from '../../static/images/corte-cabelho-m.jpg';
import Modal from '@material-ui/core/Modal';

import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';

import AccessTimeIcon from '@material-ui/icons/AccessTime';
import EventIcon from '@material-ui/icons/Event';
import InfoIcon from '@material-ui/icons/Info';
import TimerIcon from '@material-ui/icons/Timer';

import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import Divider from '@material-ui/core/Divider';

import Button from '@material-ui/core/Button';

import EmailIcon from '@material-ui/icons/Email';
import PhoneIcon from '@material-ui/icons/Phone';
import RoomIcon from '@material-ui/icons/Room';

import { APIURL } from '../../environment';

import {
    Link
  } from "react-router-dom";

import { useHistory } from "react-router-dom";
import { useGenericModel, useSnackbarsAlert, useBackdrop, useDialogs } from '../../hooks';

import moment from 'moment';

import { envia } from '../../utils';

const Concluir = ({match, location }) => {

    const [perfil, setPerfil, PerfilInicial] = useGenericModel('profile_client');

    // const [openALert, closeAlert, Alert] = useSnackbarsAlert()
    const [openBackdrop, closeBackdrop, Backdrop] = useBackdrop()
    const [openDialogs, closeDialogs, Dialogs] = useDialogs()

    const classes = useStyles();
    const [servico, setServico] = React.useState(null);
    const [dia, setDia] = React.useState(false);
    const [horario, setHorario] = React.useState(false);
    const history = useHistory();

    React.useEffect( () => {
        if(location.state?.servico){
            setServico(location.state?.servico)
        }else{
            history.push(`/${match?.params.a}`)
        }
    },[perfil])


    const handleConcluir = async () => {
        openBackdrop()
        let novoEvento = {
            user: location.state?.servico.user,
            cliente: perfil?.user,
            servico: location.state?.servico.id,
            dia: location.state?.horario,
            hora: "",
            allDay: false,
        }  
        let result = await envia("account_events", novoEvento)
        if (result){
            setTimeout(() => {
                if ( result.hasOwnProperty("id") ) {
                    openDialogs({
                        title: "Solicitado",
                        context: "Agendamento solicitado ! Você receberá um sms confirmando o agendamento.",
                        confirm: {
                            handle: () => setTimeout(() => { history.push(`/${match?.params.a}`) }, 300) ,
                            msg: "confirmar"
                          },
                        })
                }else{
                    openDialogs({
                        title: "Desculpa",
                        context: "Não foi possível realizar o agendamento.",
                        confirm: {
                            handle: () => setTimeout(() => { history.push(`/${match?.params.a}`) }, 300),
                            msg: "confirmar"
                          },
                        })
                }
                closeBackdrop()
                }, 300);
        }else{
            openDialogs({
                title: "Desculpa",
                context: "Não foi possível realizar o agendamento.",
                confirm: {
                    handle: () => setTimeout(() => { history.push(`/${match?.params.a}`) }, 300),
                    msg: "confirmar"
                  },
                })
                closeBackdrop()
        }
      };


        return (
            <Container>
                    <Main>
    
                    <div className={classes.root}>
                        <AppBar position="static" style={{ background: location.state?.cartao }}  className={classes.bar} >
                            <Toolbar  >
                            <Link to={{
                                    pathname: `/${match?.params.a}/s`,
                                    state: { 
                                        ...location.state
                                    }                                
                                }}>
                                <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                                    <ArrowBackIosIcon />
                                </IconButton>
                            </Link>
    
                            <Typography variant="h6" color="inherit">
                                Confirmar
                            </Typography>
                            </Toolbar>
                        </AppBar>
                    </div>
    
                    <WrapperProfileClient color={location.state?.cartao} >
                            <Avatar alt={perfil.user?.first_name} src={perfil?.avatar ? `${APIURL}${perfil?.avatar}` : ""} className={classes.large} />
                         <WrapperProfileinfo>
                         <Typography variant="h6"  >
                                {perfil.user?.first_name}
                            </Typography>
                         </WrapperProfileinfo>
                    </WrapperProfileClient>

                <WrapperProfileinfoList>
                    <List>
                        { 
                            perfil.user?.email ?
                            <ListItem>
                                <ListItemAvatar>
                                    <EmailIcon />
                                </ListItemAvatar>
                                <ListItemText secondary={perfil.user?.email}  />
                            </ListItem>
                            :
                            <></>
                        }


                        {
                            perfil?.telefone ?
                            <ListItem>
                                <ListItemAvatar>
                                    <PhoneIcon />
                                </ListItemAvatar>
                                <ListItemText secondary={perfil?.telefone}  />
                            </ListItem>
                            :
                            <></>
                        }

                        {
                            perfil?.endereco ? 
                                <ListItem>
                                    <ListItemAvatar>
                                        <RoomIcon />
                                    </ListItemAvatar>
                                    <ListItemText secondary={perfil?.endereco ? perfil?.endereco : ""} />
                                </ListItem>
                            : 
                           <></>
                        }
                    </List>
                    <Link to={{
                            pathname: `/profile/`,
                            state: {
                                ...location.state,
                                perfil: perfil
                            },                              
                        }}>
                        <WrapperButton> 
                            <Button  disableElevation>
                                Editar
                            </Button>
                        </WrapperButton> 
                    </Link >
                </WrapperProfileinfoList>
                
                <WrapperInfosAgenda >
                    <CardContent >
                            <Typography variant="caption" gutterBottom>
                                SERVIÇO
                            </Typography>
                    </CardContent>
                </WrapperInfosAgenda>

                    <CardServico>
                        <div>
                            <CardContent >
                            <Typography variant="h6">
                                {servico?.nome}
                            </Typography>
                            <Typography variant="body2" color="textSecondary">
                                {servico?.descricao}
                            </Typography>
                            </CardContent>
                        </div>
                        <WrapperCardMedia>
                            <CardMedia
                            className={classes.cover}
                            image={servico?.img}
                            title={servico?.nome}
                            />
                        </WrapperCardMedia>
                    </CardServico>
    
                <WrapperInfosAgenda >    
                    <InfosAgenda>
                        <ListItem>
                            <ListItemAvatar>
                                <InfoIcon />
                            </ListItemAvatar>
                            <ListItemText primary="Atividade" secondary={servico?.tipo} />
                        </ListItem>
                        <ListItem>
                            <ListItemAvatar>
                                <TimerIcon />
                            </ListItemAvatar>
                            <ListItemText primary="Tempo" secondary={servico?.tempo_atividade} />
                        </ListItem>
                    </InfosAgenda>
                </WrapperInfosAgenda>
    
                <WrapperInfosAgenda >
                    <CardContent >
                            <Typography variant="caption" gutterBottom>
                                AGENDAMENTO
                            </Typography>
                    </CardContent>
                    <InfosAgenda>
                        <ListItem>
                            <ListItemAvatar>
                                <EventIcon />
                            </ListItemAvatar>
                            <ListItemText primary="Data" secondary={ location.state?.dia ? location.state?.dia.ddmmyyyy().replace(/-/g, "/") : ""} />
                        </ListItem>
                        <ListItem>
                            <ListItemAvatar>
                                <AccessTimeIcon />
                            </ListItemAvatar>
                            <ListItemText primary="Hora" secondary={ location.state?.horario ? moment(location.state?.horario).format('H:mm') : ""} />
                        </ListItem>
                    </InfosAgenda>
                </WrapperInfosAgenda>
    

                    <WrapperButton> 
                        {
                            perfil?.telefone ?
                                <Button variant="contained" color="primary" disableElevation onClick={handleConcluir} > CONCLUIR </Button>
                                :
                                <Button variant="contained" color="primary" disableElevation disabled> CONCLUIR </Button>
                        }
                    </WrapperButton> 

                    </Main>
                    <Backdrop />
                    <Dialogs />
            </Container>
        );

}

export  { Concluir };
