import React from 'react';

import { 
    Container, 
    Main,
} from '../../styles/Layout';

import {
    CardServico, 
    useStyles,
    WrapperCardMedia,
    WrapperCalendario,
    WrapperListHorario,
    WrapperInfosAgenda,
    InfosAgenda,
    WrapperButton,
} from './styles';


import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import CorteCAbeloM from '../../static/images/corte-cabelho-m.jpg';
import Modal from '@material-ui/core/Modal';

import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';

import AccessTimeIcon from '@material-ui/icons/AccessTime';
import EventIcon from '@material-ui/icons/Event';
import InfoIcon from '@material-ui/icons/Info';
import TimerIcon from '@material-ui/icons/Timer';

import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import Divider from '@material-ui/core/Divider';

import Button from '@material-ui/core/Button';

import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';

import {
    Link
  } from "react-router-dom";

import { useHistory } from "react-router-dom";

import moment from 'moment';

const diaSemanaIndex = [
    'domingo',
    'segunda',
    'terca-feira',
    'quarta-feira',
    'quinta-feira',
    'Sexta-feira',
    'sabado',
]

Date.prototype.ddmmyyyy = function() {
    var mm = this.getMonth() + 1; // getMonth() is zero-based
    var dd = this.getDate();
    return [(dd>9 ? '' : '0') + dd, '-', (mm>9 ? '' : '0') + mm, '-', this.getFullYear()].join('');
};

const Servico = ({match, location }) => {

    const [servico, setServico] = React.useState(null);
    const history = useHistory();

    const [value, onChange] = React.useState(new Date());
    const [open, setOpen] = React.useState(false);
    const [valueOption, setValueOption] = React.useState('0');
    const classes = useStyles();

    const [dia, setDia] = React.useState(false);
    const [horario, setHorario] = React.useState(false);


    React.useEffect( () => {
        if(location.state?.servico){
            setServico(location.state?.servico)
            location.state?.dia ? setDia(location.state?.dia) : setDia(false)
            location.state?.horario ? setHorario(location.state?.horario) : setHorario(false)
        }else{
            history.push(`/${match?.params.a}`)
        }
    },[])


    const handleOpen = (date, event) => {
        setDia(date)
        setHorario(false)
        setOpen(true);
    };
    
    const handleClose = () => {
        setOpen(false);
    };

    const handleChange = (event) => {
        setValueOption(event.target.value);
    };

    return (
        <Container>
                <Main>

                    <div className={classes.root}>
                        <AppBar position="static" style={{ background: location.state?.cartao }}  className={classes.bar} >
                            <Toolbar  >
                            <Link to={`/${match?.params.a}`} >
                                <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                                    <ArrowBackIosIcon />
                                </IconButton>
                            </Link>
                            <Typography variant="h6" color="inherit">
                                {location.state?.categoria}
                            </Typography>
                            </Toolbar>
                        </AppBar>
                    </div>


                <CardServico>
                    <div>
                        <CardContent >
                        <Typography variant="h6">
                            {servico?.nome}
                        </Typography>
                        <Typography variant="body2" color="textSecondary">
                            {servico?.descricao}
                        </Typography>
                        </CardContent>
                    </div>
                    <WrapperCardMedia>
                        <CardMedia
                        className={classes.cover}
                        image={servico?.img}
                        title={servico?.nome}
                        />
                    </WrapperCardMedia>
                </CardServico>

            <WrapperInfosAgenda >

                <InfosAgenda>
                    <ListItem>
                        <ListItemAvatar>
                            <InfoIcon />
                        </ListItemAvatar>
                        <ListItemText primary="Atividade" secondary={servico?.tipo} />
                    </ListItem>
                    <ListItem>
                        <ListItemAvatar>
                            <TimerIcon />
                        </ListItemAvatar>
                        <ListItemText primary="Tempo" secondary={servico?.tempo_atividade} />
                    </ListItem>
                </InfosAgenda>
            </WrapperInfosAgenda>

                <Divider />

            <WrapperCalendario>
                <CardContent >
                    <Typography variant="caption" gutterBottom>
                        SELECIONE UMA DATA
                    </Typography>
                </CardContent>
                <Calendar
                    className={classes.reactCalendar}
                    onChange={onChange}
                    onClickDay={handleOpen}
                    value={value}
                    tileDisabled={({activeStartDate, date, view }) => (
                        date >= new Date() ? servico?.semana.map( semana => diaSemanaIndex.indexOf(semana.slug) ).filter( id => id === date.getDay() ? true : false ).length > 0 ? false : true : date.getDate() >= new Date().getDate() ? false : true 
                    )}
                />
            </WrapperCalendario>

            <WrapperInfosAgenda >
                <CardContent >
                        <Typography variant="caption" gutterBottom>
                            AGENDAMENTO
                        </Typography>
                </CardContent>
                <InfosAgenda>
                    <ListItem>
                        <ListItemAvatar>
                            <EventIcon />
                        </ListItemAvatar>
                        <ListItemText primary="Data" secondary={ dia ? dia.ddmmyyyy().replace(/-/g, "/") : ""} />
                    </ListItem>
                    <ListItem>
                        <ListItemAvatar>
                            <AccessTimeIcon />
                        </ListItemAvatar>
                        <ListItemText primary="Hora" secondary={ horario ? moment(horario).format('H:mm') : ""} />
                    </ListItem>
                </InfosAgenda>
            </WrapperInfosAgenda>

                <Link to={{
                        pathname: `/${match?.params.a}/c`,
                        state: { 
                            ...location.state,
                            dia: dia,
                            horario: horario,
                            goback: `/${match?.params.a}/s`,
                            goTo: `/${match?.params.a}/c`,
                        }                                
                    }}>
                <WrapperButton> 
                    {
                        dia && horario ? 
                        <Button variant="contained" color="primary" disableElevation> AGENDAR </Button>
                        :
                        <Button variant="contained" color="primary" disableElevation disabled > AGENDAR </Button>
                    }
                </WrapperButton> 
            </Link >

            <Modal
                open={open}
                className={classes.modal}
                onClose={handleClose}
                aria-labelledby="simple-modal-title"
                aria-describedby="simple-modal-description">
                <Body dia={dia} servico={servico} horario={horario} setHorario={setHorario} handleClose={handleClose} />
            </Modal>

                </Main>
        </Container>
    );
}


const Body = ({dia, servico, horario, setHorario, handleClose}) => {
   
    const [lishorarios, setHorarios] = React.useState([])

    React.useEffect(() => {
        initListaHoras()
    },[])

    const initListaHoras = () => {
        let semana = servico.semana.filter( item => item.slug === diaSemanaIndex[dia.getDay()] ? true : false )[0]
        
        //let event = new Date(dia.ddmmyyyy())
        let event = dia

        let inicio = semana.inicio.split(":")
        let fim = semana.fim.split(":")

        inicio = event.setHours(inicio[0], inicio[1], '00');
        fim  = event.setHours(fim[0], fim[1], '00');

        let minutos = ( parseInt(servico.tempo_atividade.split(":")[0]) * 60 ) + parseInt(servico.tempo_atividade.split(":")[1])

        const loop = (date, fim, lista, index, minutos, myloop) => {
            if( date < fim ){
                lista.push(date)
                let novoDia =  moment(date).add(minutos, 'm').toDate();
                index = index + 1;
                return myloop(novoDia, fim, lista, index, minutos, myloop)
            }
            return lista
        }

        let result = loop(inicio, fim, [], 0, minutos, loop)
        setHorarios(result)
    }

    const handleChange = (event) => {
        setHorario(lishorarios[event.target.value]);
    };

    const handleClick = (event) => {
        setTimeout(() => {
            handleClose()
        }, 200);
    };


    return (
        <WrapperListHorario >
            <FormControl component="fieldset">
                <RadioGroup aria-label="gender" name="horario-lista" value={lishorarios.indexOf(horario)} onChange={handleChange}>
                    {
                        lishorarios.map( (item, index)=> <FormControlLabel value={index} control={<Radio onClick={handleClick} />} label={ moment(item).format('H:mm') } /> )
                    }
                </RadioGroup>
            </FormControl>
        </WrapperListHorario>
    )

}


export  { Servico };


