import React from "react";
import {
  Route,
  Redirect,
} from "react-router-dom";

import { isAuthenticated } from '../utils/Auth';
import { APPLOGOUT } from '../environment';

function PrivateRoute({ children, ...rest }) {
    return (
      <Route
        {...rest}
        render={({ location }) =>
          isAuthenticated() ? (
            children
          ) : (
            <Redirect
              to={{
                pathname: "/login",
                state: {
                  ...location.state,
                  from: location 
                }
              }}
            />
          )
        }
      />
    );
  }

export default PrivateRoute;