import * as React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import { HashRouter } from 'react-router-dom'
// import DefaultLayoutNavigation from './DefaultLayoutNavigation'; 
// import PrivateRoute from './PrivateRoute';
// import Login from '../screens/Login';
// import Display from '../screens/Look/Display';

import { Home, Servico, Concluir, TelaLogin, Cadastro, Profile } from '../screens';
import PrivateRoute from './private';

import { APPURL } from '../environment';

function NavegacaoInicial() {
  return (
    <HashRouter basename={process.env.PUBLIC_URL}>
        <Switch>
            <Route path="/sign-in">
              <Cadastro />
            </Route>
            <Route path="/login" component={TelaLogin} />
            <PrivateRoute path="/profile">
              <Route path="/profile" component={Profile} /> 
            </PrivateRoute>
            <PrivateRoute path="/:a([A-Za-z]+)/c">
              <Route path="/:a([A-Za-z]+)/c" component={Concluir} /> 
            </PrivateRoute>
            <Route path="/:a([A-Za-z]+)/s" component={Servico} />
            <Route path="/:a([A-Za-z]+)" component={Home} />
            <Route path="/">
              <div> not found </div>
            </Route>
        </Switch>
    </HashRouter>
  );
}

export { NavegacaoInicial } ;