import React from 'react';
import { GOOGLECLIENTID } from '../../environment';
import { Login, ErroLogin } from '../../utils/Auth';

import GoogleLogin from 'react-google-login';
import { useHistory, useLocation } from "react-router-dom";

import {
    WrapperLogin,
    WrapperSocial,
    WrapperInput,
    WrapperOption,
    WrapperAdd,
    IconGoogle,
    IconLogIn,
    useStyles,
} from './styles';

import TextField from '@material-ui/core/TextField';
import AddPhotoAlternateIcon from '@material-ui/icons/AddPhotoAlternate';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import Button from '@material-ui/core/Button';

import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';

import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';

import {
    Link
  } from "react-router-dom";

import { 
    Container, 
    Main,
} from '../../styles/Layout';


const TelaLogin = ({match, location}) => {

    const classes = useStyles();
    let history = useHistory();
    let { from } = location.state || { from: { pathname: "/" } };
    
    const __Login = (googleResp) => {
        Login(googleResp).then( resp => {
            if(resp.result){
                history.replace( from?.state ? from : { pathname: "/profile" } );
            }else{
                console.log('Erro no login')
            }
        })
    }

    React.useEffect(()=>{
        console.log(from)
    },[])

    return (
        <Container>
                <Main>

                    <div className={classes.root}>
                        <AppBar position="static" style={{ background: location.state?.cartao }}  className={classes.bar} >
                            <Toolbar  >
                            <Link to={location.state?.goback ? location.state?.goback : "" } >
                                <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                                    <ArrowBackIosIcon />
                                </IconButton>
                            </Link>

                            <Typography variant="h6" color="inherit">
                            </Typography>
                            </Toolbar>
                        </AppBar>
                    </div>

                    <WrapperLogin color={ location.state?.cartao  ? location.state?.cartao : "#3f51b5" } >
                        <WrapperSocial >
                            
                            <GoogleLogin
                                clientId={GOOGLECLIENTID}
                                buttonText="Login"
                                onSuccess={__Login}
                                onFailure={ErroLogin}
                                cookiePolicy={'single_host_origin'}
                                render={renderProps => (
                                    <Button
                                    variant="contained"
                                    color="secondary"
                                    startIcon={<IconGoogle />}
                                    onClick={renderProps.onClick} 
                                    disabled={renderProps.disabled}
                                    style={{ width: '100%', color: "#ffffff" }}
                                >
                                        Google
                                </Button>
                                )}
                            />

                        </WrapperSocial>
                        <Divider />
                        <WrapperInput >         
                            <TextField
                                id="descricao-nome"
                                label="Email"
                                style={{ margin: '5px 0 10px 0' }}
                                placeholder=""
                                helperText="Email cadastrado"
                                fullWidth
                                margin="normal"
                                variant="outlined"
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                // value={nome}
                                // onChange={ (event) => setNome(event.target.value) }
                            />
                            <TextField
                                    id="descricao-id"
                                    label="Senha"
                                    style={{ margin:  '5px 0 0 0' }}
                                    placeholder=""
                                    helperText="Senha cadastrado"
                                    multiline
                                    fullWidth
                                    margin="normal"
                                    variant="outlined"
                                    InputLabelProps={{
                                        shrink: true,
                                    }}
                            />
                            <WrapperOption >
                                <WrapperAdd >
                                    <Button 
                                        startIcon={<IconLogIn/>}
                                        variant="contained" 
                                        color="primary" 
                                        style={{ width: '100%' }} 
                                        >
                                        Sign In
                                    </Button>
                                </WrapperAdd>
                                <WrapperAdd >
                                    <Typography variant="overline" display="block" gutterBottom  style={{ fontSize: '10px' }} >
                                        Esqueceu a senha ?
                                    </Typography>
                                </WrapperAdd>
                            </WrapperOption>
                        </WrapperInput>
                    </WrapperLogin>

                        

                </Main>
        </Container>
    );
}

export { TelaLogin };
