import React from 'react';


import { 
    Container, 
    Main,
} from '../../styles/Layout';

import {
  WrapperHeader,
  WrapperAdd,
  useStyles,
  WrapperButton,
} from './styles';

import { 
  Button,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  ListItemText,
  ListItemAvatar,
  TextField,
  Avatar,
  AppBar,
  Toolbar,
  IconButton,
  Typography,
} from '@material-ui/core';

import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import EmailIcon from '@material-ui/icons/Email';
import PhoneIcon from '@material-ui/icons/Phone';
import RoomIcon from '@material-ui/icons/Room';
import PersonIcon from '@material-ui/icons/Person';
import WorkIcon from '@material-ui/icons/Work';
import InstagramIcon from '@material-ui/icons/Instagram';
import FacebookIcon from '@material-ui/icons/Facebook';
import FaceIcon from '@material-ui/icons/Face';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';

import { useGenericModel } from '../../hooks';
import { atualiza, logout } from '../../utils';
import { APIURL } from '../../environment';


import {
    Link
  } from "react-router-dom";

import { useHistory } from "react-router-dom";

const Profile = ({match, location }) => {

  const [perfil, setPerfil, PerfilInicial] = useGenericModel('profile_client');
  const [input, setInput] = React.useState(false)
  // const [perfil, setPerfil] = React.useState({})

  const history = useHistory();
  const classes = useStyles();

  React.useEffect(() => {
    // console.log(location)
    if(location.state?.perfil){
      let perfilInicial = {...location.state?.perfil}
      delete perfilInicial.avatar;
      setPerfil(location.state?.perfil)
      setInput(perfilInicial)
    }else{
      let perfilInicial = {...perfil}
      delete perfilInicial.avatar;
      setInput(perfil)
      // history.push( location.state?.goback ? `${location.state?.goback}` : '/')
    }
  },[perfil]);


  const list_validae = {
    text: (value) =>  { return { value: true, msg: "" } },
    email: (value) => { return { value: true, msg: "" } },
    telefone: (value) => { return { value: true, msg: "" } }
  }

  const validaInput = (value, type, key, customObj, customKey) => {
      let result = list_validae[type](value)
      if( result.value ){
          if(customObj){
              customObj[customKey] = value
              let novoInput = {...input}
              novoInput[key] = customObj
              setInput(novoInput)
          }else{
              let novoInput = {...input}
              novoInput[key] = value
              setInput(novoInput)
          }
      }else {
        console.log(result.msg)
      }
  }

  async function enviaFormularioProfile(){
    let novoInput = {...input}
    let result = await atualiza('profile_client', novoInput)
    setPerfil(result)
    // console.log('[Resultado...]', result)
  }

  const handleLogout = () => {

    logout( () => history.push({
        pathname: location.state?.goback ? location.state?.goback : "/",
        state: {
            ...location.state,
        },                              
    }));
}

  return (
    <Container>
        <Main>

        <div className={classes.root}>
            <AppBar position="static" style={{ background: location.state?.cartao }}  className={classes.bar} >
                <Toolbar  >
                <Link to={{
                            pathname: location.state?.goTo ? location.state?.goTo : "/",
                            state: {
                                ...location.state,
                            },                              
                        }}>
                    <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                        <ArrowBackIosIcon />
                    </IconButton>
                </Link>

                <Typography variant="h6" color="inherit">
                    
                </Typography>
                </Toolbar>
            </AppBar>
        </div>

                <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="acordion-avatar"
                      >
                        <ListItemAvatar>
                          {
                            perfil?.avatar ? 
                              <Avatar alt={input.user?.first_name} src={`${APIURL}${perfil?.avatar}`} style={{ width: 40, height: 40}} />
                            :
                              <FaceIcon />
                          }
                        </ListItemAvatar>
                          <ListItemText primary="Avatar" secondary="" />
                    </AccordionSummary>
                    <AccordionDetails>
                      <input type="file" onChange={ 
                        (event) => {
                          let novoInput = {...input}
                          novoInput['avatar'] = event.target.files[0]
                          setInput(novoInput)
                        }
                    } /> 
                    </AccordionDetails>
                </Accordion>

                <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        id="acordion-nome"
                      >
                        <ListItemAvatar>
                            <PersonIcon />
                        </ListItemAvatar>
                        <ListItemText primary="Nome" secondary={input.user?.first_name} />
                    </AccordionSummary>
                    <AccordionDetails>
                      <TextField 
                        fullWidth 
                        id="input-nome" 
                        label={ input.user?.first_name ? false : "Nome" } 
                        variant="outlined" 
                        size="small"
                        value={ input.user?.first_name ? input.user?.first_name : "" }
                        onChange={ (event)=> validaInput( event.target.value, "text", "user", input.user, "first_name" )} 
                      /> 
                    </AccordionDetails>
                </Accordion>

                {/* <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        id="acordion-profissao"
                      >
                        <ListItemAvatar>
                            <WorkIcon />
                        </ListItemAvatar>
                        <ListItemText primary="Profissão" secondary={input?.profissao} />
                    </AccordionSummary>
                    <AccordionDetails>
                      <TextField 
                        fullWidth 
                        id="input-profissao" 
                        label={ input?.profissao ? false : "Profissão" } 
                        variant="outlined" 
                        size="small"
                        value={input?.profissao ?  input.profissao : "" }
                        onChange={ (event)=> validaInput( event.target.value, "text", "profissao" ) } 
                        /> 
                    </AccordionDetails>
                </Accordion> */}

                <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="acordion-email"
                      >
                        <ListItemAvatar>
                            <EmailIcon />
                        </ListItemAvatar>
                        <ListItemText primary="E-mail" secondary={ input.user?.email } />
                    </AccordionSummary>
                    <AccordionDetails>
                      <TextField 
                        fullWidth 
                        id="input-email" 
                        label={ input.user?.email ? false : "E-mail" } 
                        variant="outlined" 
                        size="small" 
                        value={ input.user?.email ?  input.user?.email : "" }
                        onChange={ (event)=> validaInput( event.target.value, "email", "user", input.user, "email" ) }                        
                        /> 
                    </AccordionDetails>
                </Accordion>
  
                <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="acordion-telefone"
                      >
                      <ListItemAvatar>
                          <PhoneIcon />
                      </ListItemAvatar>
                      <ListItemText primary="Telefone" secondary={input?.telefone} />
                    </AccordionSummary>
                    <AccordionDetails> 
                    <TextField 
                      fullWidth 
                      id="input-telefone" 
                      label={ input?.telefone ? false : "Telefone" } 
                      variant="outlined" 
                      size="small"
                      value={ input?.telefone ? input?.telefone: "" }
                      onChange={ (event)=> validaInput( event.target.value, "telefone", "telefone" ) }                        
                      /> 
                    </AccordionDetails>
                </Accordion>              

              <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="acordion-endereco"
                      >
                      <ListItemAvatar>
                          <RoomIcon />
                      </ListItemAvatar>
                      <ListItemText primary="Endereço" secondary={ input?.endereco } />
                    </AccordionSummary>
                    <AccordionDetails> 
                    <TextField 
                      fullWidth 
                      id="input-endereco" 
                      label={ input?.endereco ? false : "Endereço" }
                      variant="outlined" 
                      size="small"
                      value={ input?.endereco ? input?.endereco : "" }
                      onChange={ (event)=> validaInput( event.target.value, "text", "endereco" ) }                         
                      /> 
                    </AccordionDetails>
                </Accordion>                

              <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="acordion-instagram"
                      >
                      <ListItemAvatar>
                          <InstagramIcon />
                      </ListItemAvatar>
                      <ListItemText primary="Instagram" secondary={ input?.instagram } />
                    </AccordionSummary>
                    <AccordionDetails> 
                    <TextField 
                        fullWidth 
                        id="input-instagram" 
                        label={ input?.instagram ? false : "Instagram" }  
                        variant="outlined" 
                        size="small" 
                        value={ input?.instagram ? input?.instagram : "" }
                        onChange={ (event)=> validaInput( event.target.value, "text", "instagram" ) }                      
                      /> 
                    </AccordionDetails>
                </Accordion> 

              <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="acordion-facebook"
                      >
                      <ListItemAvatar>
                          <FacebookIcon />
                      </ListItemAvatar>
                      <ListItemText primary="Facebook" secondary={ input?.facebook } />
                    </AccordionSummary>
                    <AccordionDetails> 
                    <TextField 
                        fullWidth 
                        id="input-facebook" 
                        label={ input?.facebook ? false : "Facebook" } 
                        variant="outlined" 
                        size="small" 
                        value={ input?.facebook ? input?.facebook : "" }
                        onChange={ (event)=> validaInput( event.target.value, "text", "facebook" ) }                          
                        /> 
                    </AccordionDetails>
                </Accordion> 

                <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="acordion-facebook"
                      >
                      <ListItemAvatar>
                          <ExitToAppIcon />
                      </ListItemAvatar>
                      <ListItemText primary="Logout" />
                    </AccordionSummary>
                    <AccordionDetails> 
                        <WrapperButton>
                            <Button variant="contained" color="primary" disableElevation onClick={handleLogout}  >
                                SAIR
                            </Button>
                        </WrapperButton>
                    </AccordionDetails>
                </Accordion> 

                    <WrapperButton> 
                        <Button variant="contained" color="primary" disableElevation onClick={enviaFormularioProfile} >
                            ATUALIZAR
                        </Button>
                    </WrapperButton> 

            </Main>
        </Container>
  );

}

export { Profile }